<template>
    <div class="avatar">
        <div class="pan-info">
            <slot></slot>
        </div>
        <img :src="image" alt="" />
    </div>
</template>

<script>
    export default {
        props:['image'],
        methods: {

        }
    }
</script>

<style lang="scss">
    .avatar{
        padding:0.2rem;
        cursor:pointer;
        position:relative;
        width:2rem;
        height:2rem;
        .pan-info{
            position:absolute;
            width: inherit;
            height: inherit;
            color:#000;
            z-index:1;
            border-radius: 50%;
            overflow: hidden;
            box-shadow: inset 0 0 0 5px rgba(0, 0, 0, 0.05);
           text-align: center;
            div{
                margin-top:0.8rem;
            }

        }
        img{
            position:relative;
            z-index:2;
            width:2rem;
            height:2rem;
            border-radius:100%;
            transition: all 0.5s;
        }
    }
    .avatar:hover img{
        transform-origin: right bottom;
        transform: rotate(90deg);
        transition: all 0.5s;
    }
</style>



