<template>
    <div class="editContainer">
        <div style="display: flex">
            <avatar :image="loginMsg.avatar" >
                <div>{{loginMsg.status}}</div>
                <strong>{{loginMsg.name}}</strong>
            </avatar>
            <pannel :pannel="loginMsg"></pannel>
        </div>
        <div class="button">
            <el-button type="info"  @click="handleChangeLimit">切换权限</el-button>
        </div>
        <div class="img">
            <img class='emptyGif' :src="emptyGif">
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import avatar from './avatar.vue';
    import pannel from './pannel.vue'
    export default {
        data () {
            return {
                emptyGif:'https://wpimg.wallstcn.com/0e03b7da-db9e-4819-ba10-9016ddfdaed3'
            }
        },
        computed:mapGetters(['loginMsg']),
        methods: {
            handleChangeLimit(){
                this.$store.dispatch("changeLogin")
            }
        },
        components:{
            avatar,
            pannel
        }
    }
</script>

<style lang="scss">
    .editContainer{
        position:relative;
        background:#e3e3e3;
        width:100%;
        height:100%;
        .button{
            margin-left:0.5rem;
        }
    }
    .img{
        width:100%;
        display: flex;
        justify-content: center;
        .emptyGif{
            width:6rem;
            height:4.5rem;
        }
    }

</style>



