<template>
    <div class="superContainer">
        <avatar :image="loginMsg.avatar" >
            <div>{{loginMsg.status}}</div>
            <strong>{{loginMsg.name}}</strong>
        </avatar>
        <pannel :pannel="loginMsg"></pannel>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import avatar from './avatar.vue';
    import pannel from './pannel.vue'
    export default {
        data () {
            return {}
        },
        computed:mapGetters(['loginMsg']),
        methods: {

        },
        components:{
            avatar,
            pannel
        }
    }
</script>

<style lang="scss">
    .superContainer{
        position:relative;
        background:#e3e3e3;
        width:100%;
        height:100%;
        display: flex;
    }
</style>



